<template>
    <layout :showBack="isEdit" :title="`${isEdit ? '编辑' : '新增'}收货单信息`">
        <div class="edit-container">
            <span class="title">基本信息</span>
            <van-form @submit="onSubmit">
                <van-field v-model="pageData.userName"
                    name="创建人"
                    label="创建人"
                    readonly/>
                <van-field v-model="pageData.createTime"
                    name="创建时间"
                    label="创建时间"
                    clearable
                    readonly
                    placeholder="请选择创建时间"
                    :rules="rules.createTime">
                    <template #button>
                        <van-button size="small" @click="timeShow = true" native-type="button" type="primary">选择时间</van-button>
                    </template>
                </van-field>
                <span class="title">采购信息</span>
                <van-field v-model="pageData.purchaseId"
                    name="采购单"
                    label="采购单"
                    clearable
                    readonly
                    placeholder="请选择采购单"
                    :rules="rules.purchaseId">
                    <template #button>
                        <van-button size="small" @click="purchaseShow = true" native-type="button" type="primary">选择采购单</van-button>
                    </template>
                </van-field>
                <van-field v-model="pageData.supplierName"
                    name="供应商"
                    label="供应商"
                    readonly/>
                <van-field v-model="pageData.materialName"
                    name="材料名"
                    label="材料名"
                    readonly/>
                <van-field v-model="pageData.materialWeight"
                    name="数量"
                    label="数量"
                    readonly/>
                <van-field v-model="pageData.materialUnit"
                    name="计量单位"
                    label="计量单位"
                    readonly/>
                <van-field v-model="pageData.price"
                    name="价格"
                    label="价格"
                    readonly/>
                <van-field v-model="pageData.supplierContact"
                    name="联系人"
                    label="联系人"
                    readonly/>
                <van-field v-model="pageData.supplierPhone"
                    name="联系方式"
                    label="联系方式"
                    readonly/>
                <span class="title">备注信息</span>
                <van-field v-model="pageData.remark"
                    name="备注"
                    label="备注"
                    type="textarea"
                    placeholder="请输入备注"/>
                <div class="btn">
                    <van-button round block type="primary" native-type="submit">提交</van-button>
                </div>
            </van-form>
        </div>

        <van-popup v-model="timeShow"
            position="bottom"
            :close-on-click-overlay="false"
            :style="{ height: '309px' }">
            <van-datetime-picker @cancel="timeShow = false"
                @confirm="timePickEvt"
                type="datehour"
                title="选择创建时间"/>
        </van-popup>

        <van-popup v-model="purchaseShow"
            position="bottom"
            :style="{ height: '100%' }">
            <purchase-picker @pickEvent="pickPurchaseEvt"/>
        </van-popup>
    </layout>
</template>

<script>
    import { receiveAddApi, receiveInfoApi, receiveSaveApi } from '../../apis/ReceiveApi'
    import { formatDate } from '../../util'

    export default {
        data() {
            return {
                timeShow: false,
                purchaseShow: false,
                isEdit: false,
                pageData: {
                    id: '',
                    userId: '',
                    userName: '',
                    createTime: '',
                    projectId: '',
                    projectName: '',
                    purchaserId: '',
                    purchaserName: '',
                    purchaseId: '',
                    supplierId: '',
                    supplierName: '',
                    supplierContact: '',
                    supplierPhone: '',
                    materialId: '',
                    materialName: '',
                    materialUnit: '',
                    materialWeight: '',
                    price: '',
                    remark: ''
                },
                rules: {
                    createTime: [{required: true, message: '选择创建日期'}],
                    purchaseId: [{required: true, message: '选择采购单'}]
                },
                unitOption: [
                    {text: 'Kg', value: 'Kg'},
                    {text: '吨', value: '吨'},
                    {text: '方', value: '方'}
                ]
            }
        },
        computed: {
            userInfo() {
                return this.$store.getters['common/getUserInfo']
            }
        },
        async created() {
            let id = this.$route.query.id
            this.isEdit = !!id
            if (this.isEdit) {
                let result = await receiveInfoApi(id)
                if (result.code !== 200) {
                    this.isEdit = false
                    this.$toast.fail('编辑数据信息不存在')
                    return
                }
                for (let name in this.pageData) {
                    this.pageData[name] = result.data[name] || ''
                }
                this.pageData.remark = this.pageData.remark.constructor === Array ? this.pageData.remark[0].remark : ''
                this.pageData.createTime = formatDate(this.pageData.createTime, 'yyyy-MM-dd hh:mm:ss')
            } else {
                this.pageData.userId = this.userInfo.id
                this.pageData.userName = this.userInfo.name
            }
        },
        methods: {
            async onSubmit() {
                let obj = {
                    ...this.pageData
                }

                let result
                if (this.isEdit) {
                    result = await receiveSaveApi(obj)
                } else {
                    result = await receiveAddApi(obj)
                }
                if (result.code === 200) {
                    this.$toast.success(`${this.isEdit ? '编辑' : '新增'}收货单信息成功`)
                } else {
                    this.$toast.fail(`${this.isEdit ? '编辑' : '新增'}收货单信息失败`)
                }
            },
            timePickEvt(val) {
                this.pageData.createTime = formatDate(val, 'yyyy-MM-dd hh:mm:ss')
                this.timeShow = false
            },
            pickPurchaseEvt(row) {
                if (!!row) {
                    this.pageData.projectId = row.projectId
                    this.pageData.projectName = row.projectName
                    this.pageData.purchaserId = row.createId
                    this.pageData.purchaserName = row.createName
                    this.pageData.purchaseId = row.id
                    this.pageData.supplierId = row.supplierId
                    this.pageData.supplierName = row.supplierName
                    this.pageData.supplierContact = row.supplierContact
                    this.pageData.supplierPhone = row.supplierPhone
                    this.pageData.materialId = row.materialId
                    this.pageData.materialName = row.materialName
                    this.pageData.materialUnit = row.materialUnit
                    this.pageData.materialWeight = row.materialWeight
                    this.pageData.price = row.price
                }
                this.purchaseShow = false
            }
        }
    }
</script>